.header__nav-link_active {
  padding: 5px 0 0 0;
}

.header__nav-link_active::after {
  content: '';
  position: relative;
  display: block;
  border: 1px solid;
  top: 24px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
	.header__nav-link_active::after {
    top: 19px;
  }
}
